import PCUI from './component/index'
import H5UI from './H5component/index'
import { isMobile } from "./common/Utils";
import getLang from "./common/lang/index";
import { Promise } from "es6-promise";

export default class VhallInterface {
  constructor(param) {
    this.language = getLang(param.store)
    window.vp = this.vhallplayer = param.vhallplayer; // => VideoModule instance
    this.isLive = param.isLive;
    if (param.isLive) {
      this.live_type = param.live_type;
      this.live_subtitle = param.live_subtitle;
    } else {
      this.vod_subtitle = param.vod_subtitle;
    }
    this.videoNode = param.videoNode;
    this.isLimitSeek = false;   // seek
    this.limitSeekMax = 0;  // seek
    this.vhallplayer.on('seeked', res => {
      if (this.isLimitSeek) {
        const seekMax = this.vhallplayer.getPlayed().length >= 1 ? Math.max(this.limitSeekMax, this.vhallplayer.getPlayed().end(this.vhallplayer.getPlayed().length - 1)) : this.isLimitSeek;    // seek
        if (this.vhallplayer.getCurrentTime() > seekMax) {
          this.vhallplayer.setCurrentTime(seekMax);
        }
      }
    });

    if (!isMobile()) {
      new PCUI(this.videoNode, this, param.markPoints);
    } else {
      new H5UI(this.videoNode, this, param.markPoints);
    }
  }

  /**
   * 
   * @param {string} eventName 
   * @param {function} handler 
   */
  on(eventName, handler) {
    this.vhallplayer.on(eventName, handler);
  }

  /**
   * 
   */
  play() {
    return this.vhallplayer.play();
  }

  /**
   * 
   */
  pause() {
    this.vhallplayer.pause();
  }

  /**
   * 
   */
  getIsPause() {
    return this.vhallplayer.getIsPaused();
  }

  /**
   * 
   * @param {function} failure 
   */
  getLoop(failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    return this.vhallplayer.getLoop();
  }

  /**
   * 
   * @param {boolean} isLoop 
   * @param {function} failure 
   */
  setLoop(isLoop, failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    if (typeof isLoop !== "boolean") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    this.vhallplayer.setLoop(isLoop);
  }

  /**
   * 
   * @return {Array} 
   */
  getQualitys() {
    if (this.vhallplayer._config && this.vhallplayer._config.liveOption && this.vhallplayer._config.liveOption.type === 'rtc') {
      return [
        {
          cdnName: "",
          def: "high",
          idx: '',
          line: "",
          url: ""
        }, {
          cdnName: "",
          def: "low",
          idx: '',
          line: "",
          url: ""
        }
      ]
    } else {
      return this.vhallplayer.currentDefinitionList || [];
    }
  }

  /**
   * 
   */
  getCurrentQuality() {
    if (this.vhallplayer._config && this.vhallplayer._config.liveOption && this.vhallplayer._config.liveOption.type === 'rtc') {
      return {
        cdnName: "",
        def: "high",
        idx: '',
        line: "",
        url: ""
      }
    } else {
      return this.vhallplayer.currentDefinition || {};
    }
  }

  /**
   * 
   * @param {object} obj 
   * @param {function} failure 
   */
  setQuality(obj, failure) {
    if (Object.prototype.toString.call(obj) !== '[object Object]') {
      failure && failure({ code: '611001', message: "", data: {} });
      return Promise.reject(new Error(''));
    }
    // SDK
    if (this.vhallplayer.currentDefinition && this.vhallplayer.currentDefinition.url === obj.url) return Promise.reject(', ');
    // this.vhallplayer.currentDefinition = obj; 2020 - 11 -13
    if (this.vhallplayer._config && this.vhallplayer._config.x5 && this.vhallplayer.player && this.vhallplayer.player._type !== 'NativePlayer' && this.vhallplayer.isFullscreen) {
      // 
      this.vhallplayer.exitFullscreen();
      setTimeout(() => { return this.vhallplayer.setQuality(obj) }, 200);
    } else {
      return this.vhallplayer.setQuality(obj);
    }
  }

  /**
   * 
   * @param {function} failure 
   */
  enterFullScreen(failure) {
    if (this.vhallplayer.isFullscreen) {
      failure && failure({ code: '611005', message: "", data: {} });
      return;
    }
    this.vhallplayer.enterFullscreen();
    try{
      this.vhallplayer.clearBarrage()
    } catch(e) {
    }
  }

  /**
   * 
   * @param {function} failure 
   */
  exitFullScreen(failure = () => { }) {
    // if (!this.vhallplayer.isFullscreen) {
    //   failure && failure({ code: '611006', message: "", data: {} });
    //   return;
    // }
    this.vhallplayer._clickExit = true;

    try{
      this.vhallplayer.clearBarrage()
    } catch(e) {
    }

    this.vhallplayer.exitFullscreen();
  }

  /**
   * 
   */
  isFullscreen() {
    return this.vhallplayer.isFullscreen;
  }

  /**
   * 
   * @param {boolean} isMute 
   * @param {function} failure 
   */
  setMute(isMute, failure) {
    if (typeof isMute !== "boolean") {
      failure && failure({ code: '611001', message: "", data: {} })
      return;
    }
    this.vhallplayer.setMuted(isMute);
  }

  /**
   * 
   */
  getVolume() {
    return this.vhallplayer.getVolume() * 100;
  }
  /**
   * 
   * @param {int} volume  0~100
   * @param {function} failure 
   */
  setVolume(vol, failure) {
    if (typeof vol !== "number") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    if (vol < 0 || vol > 100) {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    this.vhallplayer.setVolume(vol / 100);
  }

  /**
   * 
   * @param {function} failure 
   */
  getDuration(failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    return this.vhallplayer.getDuration();
  }

  /**
   * 
   * @param {function} failure 
   */
  getCurrentTime(failure) {
    if (this.isLive && (!this.vhallplayer._config.isOpenTimeshift || (this.vhallplayer._config.isOpenTimeshift && this.vhallplayer._config.type !== 'hls'))) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    return this.vhallplayer.getCurrentTime();
  }


  /**
   * 
   * @param {int} time 
   * @param {function} failure 
   */
  setCurrentTime(time, failure = () => { }) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    if (typeof time !== "number") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    clearTimeout(this.timerId);
    // seekseek, seek
    if (this.vhallplayer.getSeeking()) {
      this.timerId = setTimeout(() => {
        this.vhallplayer.setCurrentTime(time || 0);
      }, 200);
      return;
    }
    this.vhallplayer.setCurrentTime(time || 0);
  }

  /**
   * 
   * @return {int} 0:/,1:/,2:,3:/
   */
  getNetworkState() {
    return this.vhallplayer.getNetworkState();
  }


  /**
   * 
   */
  destroy() {
    VhallMsg.emitBroadcast({ type: 'delay', data: { delay: -1 } });
    this.vhallplayer.destroy();
    // PC
    const container = document.querySelector(".vhallPlayer-container");
    // 
    const shadow = document.querySelector(".vhallPlayerh5-shadow");
    // 
    const playBtn = document.querySelector(".vh-playBtnContainer");
    container && container.parentNode.removeChild(container);
    shadow && shadow.parentNode.removeChild(shadow);
    playBtn && playBtn.parentNode.removeChild(playBtn);
  }

  /**
   * 
   * @return {object} 
   * @param {function} failure 
   */
  getUsableSpeed(failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    return this.vhallplayer.playbackRateList;
  }

  /**
   * 
   * @param {string} speed 
   * @param {function} failure 
   */
  setPlaySpeed(speed, failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    const playbackRateList = this.vhallplayer.playbackRateList;
    if (typeof speed !== "number") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }

    if (playbackRateList.indexOf(speed) === -1) {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    this.vhallplayer.setPlaybackRate(speed);
  }

  /**
   * 
   */
  videoScreenshot() {
    return this.vhallplayer.capture();
  }

  /**
   * 
   */
  openBarrage() {
    this.vhallplayer.openBarrage();
  }

  /**
   * 
   */
  closeBarrage() {
    this.vhallplayer.closeBarrage();
  }

  /**
   * 
   */
  clearBarrage() {
    this.vhallplayer.clearBarrage();
  }

  /**
   * 
   * @return {string} 
   */
  getBarrageFPS() {
    return this.vhallplayer.barrageFPS;
  }

  /**
   * 
   * @param {string} content 
   * @param {Object} setting 
   * @param {function} failure 
   */
  addBarrage(content, setting, failure) {
    const reg = /<(?!img).*?>/gi;
    content = content.replace(reg, '');
    let cache = content.replace(/<(img).*?>/gi, '');
    let fail = null;
    if(setting && (typeof setting == 'function')) {
      fail = setting;
    }
    if (failure) {
      fail = failure
    }
    //  
    if (!content) {
      fail && fail({ code: '611001', message: "", data: {} });
      return;
    }
    if(cache.length > 150) {
      fail && fail({ code: '611001', message: "150", data: {} });
      return;
    }

    if (setting && (typeof setting != 'function')) {
      let success = true;
      Object.keys(setting).forEach((key) => {
        if(!setting[key]) {
          success = false;
        }
      })
      if (!success) {
        fail && fail({ code: '611001', message: "", data: {} });
        return;
      }
      if(setting.fontSize && (setting.fontSize > 50 || setting.fontSize< 12)) {
        fail && fail({ code: '611001', message: "5012", data: {} });
        return;
      }
    }

    this.vhallplayer.addBarrage(content, setting)
  }

  /**
   * 
   * @param {object} opt  {position:(0|1|2|3)      , alpha:(0~1) , fontsize:(int) , color:(#ffffff) }
   */

  setBarrageInfo(opt, failure) {
    // To do
    // change this function
    // if (typeof opt !== "object" && (!opt.hasOwnProperty('position') || !opt.hasOwnProperty('alpha') || !opt.hasOwnProperty('fontsize') || !opt.hasOwnProperty('color'))) {
    //   failure && failure({ code: '611001', message: "", data: {} });
    //   return;
    // }
    // const { position, alpha, fontsize, color } = opt;
    // typeof position === "number" && (this.vhallplayer.barragePosition = position);
    // alpha && (this.vhallplayer.barrageAlpha = alpha);
    // fontsize && (this.vhallplayer.barrageFontsize = fontsize);
    // color && (this.vhallplayer.barrageColor = color);

    if (typeof opt !== 'object') {
      failure && failure({ code: '611001', message: "", data: {} })
      return Promise.reject(new Error({ code: '611001', message: "", data: {} }));
    }

    // duration: 12, // ,(Math.random() * duration / 3)>>>0
    // position: 3, // 0:top,10%~~50%, 1middle,30%~70% 2bottom,50%~90% 3:all,10% -- 
    // fontsize: 18, //  - 
    // alpha: 0.8,
    // font: 'Microsoft YaHei',
    // color: '#FFF',

    if(opt.position) {
      switch(~opt.position) {
        case 0:
          opt.positionRange = [0.1, 0.5]
          break;
        case 1:
          opt.positionRange = [0.3, 0.7]
          break;
        case 2:
          opt.positionRange = [0.5, 0.9]
          break;
        case 3:
          opt.positionRange = [0.1, 0.9]
          break;
        default:
          opt.positionRange = [0.1, 0.5]
          break;
      }
    }

    if(opt.fontsize) {
      if (!opt.style) opt.style = {};
        opt.style.fontSize = opt.fontsize;
    }
    if(opt.alpha) {
      if (!opt.style) opt.style = {};
      opt.style.opacity = opt.alpha;
    }

    if(opt.font) {
      if (!opt.style) opt.style = {};
      opt.style.fontFamily = opt.font;
    }

    if(opt.color) {
      if (!opt.style) opt.style = {};
      opt.style.color = opt.color;
    }

    this.vhallplayer.setBarrageInfo(opt);

  }


  /**
   * 
   */
  getVersion() {
    return this.vhallplayer.version;
  }

  /**
   * 
   */
  getDownloadSize() {
    return this.vhallplayer.getDownloadSize();
  }


  /**
   * seek
   * @param {object} opt seek
   * @param {function} failure 
   */
  setLimitSeek(opt, failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    if (typeof opt !== "object" && (!opt.hasOwnProperty('enable') || !opt.hasOwnProperty('maxTime'))) {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    if (typeof opt.enable !== "boolean" || typeof opt.maxTime !== "number") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }

    this.isLimitSeek = opt.enable;   // seek
    this.limitSeekMax = opt.maxTime;
  }

  /**
   * 
   * @param {boolean} boolean 
   */
  openControls(boolean) {
    this.vhallplayer.setControls(boolean);
  }


  /**
   * UI
   * @param {boolean} boolean UI
   */
  openUI(boolean) {
    const _isMobile = isMobile()
    const UI = _isMobile ? document.querySelector(".vhallPlayerh5-shadow") : document.querySelector(".vhallPlayer-container");
    boolean ? UI.classList.remove("close") : UI.classList.add("close");
    if (_isMobile) {
      const playBtn = document.querySelector(".vh-playBtnContainer");
      boolean ? (playBtn && playBtn.classList.remove("close")) : (playBtn && playBtn.classList.add("close"));
    }
  }

  /**
   * seek
   * @param {function} failure 
   */
  getLimitSeek(failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    return { enable: this.isLimitSeek, maxTime: this.limitSeekMax };
  }


  /**
   * 
   * @param {object} opt 
   * @param {function} failure 
   */
  setSize(opt, failure) {
    const { width, height } = opt;
    if (!opt.hasOwnProperty("width") || !opt.hasOwnProperty("height") || typeof width !== "number" || typeof height !== "number") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    this.vhallplayer.setSize(width, height);
  }


  /**
   * buffered
   */
  getBuffered() {
    return this.vhallplayer.getBuffered();
  }

  /**
   * 
   */
  getEstimateNetSpeed() {
    return this.vhallplayer.getEstimateNetSpeed();
  }

  /**
   * 
   */
  handleTimeshiftPlay(opts, failure) {
    const { delay } = opts
    if (delay < 0) {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    this.vhallplayer.handleTimeshiftPlay(opts);
  }

  /**
   * cdn
   */
  getCdnName() {
    return this.vhallplayer.getCdnName();
  }

  /**
   * 
   */
  openSubtitle() {
    this.vhallplayer.openSubtitle();
  }

  /**
   * 
   */
  closeSubtitle() {
    this.vhallplayer.closeSubtitle();
  }

  /**
   * 
   */
  getCurrentSubtitle(failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    return this.vhallplayer.getCurrentSubtitle();
  }

  /**
   * 
   */
  getSubtitleList(failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    return this.vhallplayer.getSubtitleList();
  }

  /**
   * 
   */
  switchSubtitle(opt, failure) {
    if (this.isLive) {
      failure && failure({ code: '611004', message: "", data: {} });
      return;
    }
    if (typeof opt !== "object") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    if (opt === this.getCurrentSubtitle()) {
      return;
    }
    this.vhallplayer.switchSubtitle(opt);
  }

  /**
   * 
   */
  editMarquee(opts = {}, failure = () => { }) {
    if (typeof opts !== "object") {
      failure && failure({ code: '611001', message: "", data: {} });
      return;
    }
    const { text, size = 18, interval = 20 } = opts;
    if (!text || (text && text.length > 64)) {
      failure && failure({ code: '611001', message: "", data: { detail: '64' } });
      return;
    }
    if (parseInt(size) < 1 || parseInt(size) > 200) {
      failure && failure({ code: '611001', message: "", data: { detail: '1200' } });
      return;
    }
    if (parseInt(interval) < 0 || parseInt(interval) > 300) {
      failure && failure({ code: '611001', message: "", data: { detail: '0300' } });
      return;
    }
    this.vhallplayer.editMarquee(opts);
  }

  /**
   * 
   */
  openLiveSubtitle(failure) {
    if (!this.isLive) {
      failure && failure({ code: '611003', message: "", data: {} });
      return;
    }
    this.vhallplayer.openLiveSubtitle();
  }

  /**
   * 
   */
  closeLiveSubtitle(failure) {
    if (!this.isLive) {
      failure && failure({ code: '611003', message: "", data: {} });
      return;
    }
    this.vhallplayer.closeLiveSubtitle();
  }
}
