import SDK from '../h5_player/src/VideoModule'
import VhallInterface from './VhallInterface'
import { loadMsgScript, getType, hasUsableSWF, systemProbe, judgeX5 } from './common/Utils'
import axios from 'axios'
import { EVENT, KV } from './common/Constant'
import API from './message/Api'
import Store from './common/Store'

const VhallPlayer = {
  /**
   * 
   * @param {*} opt
   * @param {*} success
   * @param {*} failure
   */
  probe(opt = {}, success = () => { }, failure = () => { }) {
    const info = {};
    try {
      const video = document.createElement('video');
      // video
      info["HTMLVideoElement"] = !!(video && video.canPlayType);
      // h.264
      info["H264"] = !!(video && video.canPlayType && video.canPlayType('video/mp4; codecs="avc1.42E01E, mp4a.40.2"'));
      // vp8
      info["WebM_VP8"] = !!(video && video.canPlayType && video.canPlayType('video/webm; codecs="vp8.0, vorbis"'));

      const mse = window['MediaSource'] || window['WebKitMediaSource'];
      // MSE
      info["MediaSourceExtensions"] = !!mse;
      const checkMSECompatibility = function (mimeType) {
        if (mse && !mse.isTypeSupported) {
          // When async type detection is required, fall back to canPlayType.
          return video.canPlayType(mimeType);
        } else {
          return mse && mse.isTypeSupported(mimeType);
        }
      };
      // MSEh264
      info["MSE_H264"] = checkMSECompatibility('video/mp4; codecs="avc1.4d401e"');
      // MSEvp9
      info["MSE_WebM_VP9"] = checkMSECompatibility('video/webm; codecs="vp9"');
      // websocket
      info["WebSocket"] = !!window['WebSocket'];
      // x5
      info["x5"] = !!judgeX5();
      success(info);
    } catch (e) {
      failure(e);
    }
  },

  /**
   * @description  
   * @param {danmakuSetting} danmakuSetting
   * @returns {Object} error { code: ,msg: }
   */
  checkDanmakuSetting(danmakuSetting) {
    if (typeof danmakuSetting != 'object') {
      throw new Error({
        code: '611001', message: ''
      })
    }
    if (danmakuSetting.style.fontSize && (~danmakuSetting.style.fontSize > 50 || ~danmakuSetting.style.fontSize < 12)) {
      throw new Error({
        code: '611001', message: ','
      })
    }

    if (danmakuSetting.style.opacity && ~danmakuSetting.style.opacity > 100 || ~danmakuSetting.style.opacity < 0) {
      throw new Error({
        code: '611001', message: ','
      })
    }

    if (danmakuSetting.style.opacity && ~danmakuSetting.style.opacity > 100 || ~danmakuSetting.style.opacity < 0) {
      throw new Error({
        code: '611001', message: ','
      })
    }
  },

  /**
   * 
   * @param {object} opt 
   * @param {function} success 
   * @param {function} failure 
   */
  async createInstance(opt, success = () => { }, failure = () => { }) {
    const { appId, accountId, token, type = '', language } = opt;
    const isLoadScript = await loadMsgScript();
    if (!isLoadScript) {
      failure && failure({ code: '611000', message: 'SDK', data: {} });
      return
    }

    let store = new Store();
    store.setKV(KV.APP_ID, appId);
    store.setKV(KV.THIRD_PARTY_USER_ID, accountId);
    store.setKV(KV.ACCESS_TOKEN, token);
    // - -! ??????
    store.setKV(KV.PACKAGE_CHECK, 'peter');
    //
    store.setKV(KV.CLIENT, 'pc_browser');
    store.setKV(KV.LANGUAGE, language);
    let api = new API(store);

    if (!accountId || typeof type !== 'string') {
      failure && failure({ code: '611001', message: '', data: {} });
      return
    }

    // flash,flash
    let useSWF;
    let isRtmp = undefined;
    let forceMSE;
    if (type === 'live') {
      useSWF = opt.liveOption && opt.liveOption.useSWF;
      isRtmp = opt.liveOption && opt.liveOption.type;
      forceMSE = opt.liveOption && opt.liveOption.forceMSE;
    } else {
      useSWF = opt.vodOption && opt.vodOption.useSWF;
      forceMSE = opt.vodOption && opt.vodOption.forceMSE;
    }
    const context = systemProbe();
    if (
        ((typeof useSWF === 'undefined' && (context.browser === 'ie' || (isRtmp === 'rtmp'))) || useSWF)
        && !hasUsableSWF()
    ) {
      failure && failure({ code: '611002', message: 'flash', data: {} });
      return;
    }

    const vhallplayer = new SDK();

    let initData = null;
    let playerData = null;
    let needDecrypt = false;
    try {
      initData = await api.init();
      switch (type) {
        case 'live':
          if (
              !opt.hasOwnProperty('liveOption') ||
              !opt.liveOption.hasOwnProperty('type') ||
              !opt.liveOption.hasOwnProperty('roomId') ||
              (opt.liveOption.type !== 'flv' && opt.liveOption.type !== 'hls' && opt.liveOption.type !== 'auto' && opt.liveOption.type !== 'rtmp' && opt.liveOption.type !== 'rtc')
          ) {
            failure && failure({ code: '611001', message: '', data: {} });
            return
          }
          playerData = await api.initLive(opt.liveOption.roomId);
          break;
        case 'vod':
          if (!opt.hasOwnProperty('vodOption') || !opt.vodOption.hasOwnProperty('recordId')) {
            failure && failure({ code: '611001', message: '', data: {} });
            return
          }
          playerData = await api.initVod(opt.vodOption.recordId);
          // hls, FlashPlayer, HlsPlayer
          const isHls = (getType(playerData.default_server.uri) === 'm3u8');
          const isFlash = ((typeof useSWF === 'undefined' && context.browser === 'ie') || useSWF === true);
          // MSE && (forceMSE ||  Android  qqbrowser)
          const isMSE = context.supportMSE && (forceMSE || !(context.os === 'android' && context.browser === 'qqbrowser'));
          needDecrypt = !!(playerData && playerData.safe_server && playerData.safe_server.uri && isHls && !isFlash && isMSE);
          // console.log('ana player data:', isHls, isFlash, isMSE, needDecrypt, context.supportMSE)
          break;
        default:
          failure && failure({ code: '611001', message: '', data: {} });
          return;
      }
    } catch (err) {
      switch (err.msg) {
        case '':
          failure && failure({ code: '611009', message: 'roomId', data: {} });
          break;
        case 'AccessToken':
          failure && failure({ code: '611010', message: 'token', data: {} });
          break;
        case '':
          failure && failure({ code: '611008', message: 'appId', data: {} });
          break;
        default:
          failure && failure({ code: '611000', message: '', data: {} });
          break;
      }
      return;
    }

    // 
    const reportConfig = {
      url: initData.log_server,
      webinar_id: type === 'live' ? opt.liveOption.roomId.trim() : opt.vodOption.recordId.trim(),
      uid: playerData && playerData.log_info.uid,
      session_id: playerData && playerData.log_info.session_id,
      vid: playerData && playerData.log_info.third_party_user_id,
      vfid: playerData && playerData.log_info.account_id,
      //   paas
      ndi: '',
      // saas  paas
      guid: '',
      // saas  paas
      vtype: '',
      // saas  paas
      topic: '',
      app_id: appId,
      flow_type: 2, //   2
      bu: 1, //  pass 1
      enable: true, //
      interval: opt.hasOwnProperty('reportOption') ? opt['reportOption']['interval'] || 30 : 30,
    };

    // otherOption, 
    const otherOption = opt.hasOwnProperty('otherOption') ? opt.otherOption : {};

    let defaultDefinition = type === 'live' ? opt.liveOption.defaultDefinition : opt.vodOption.defaultDefinition;
    let schedulerOption = opt['schedulerOption'];
    let live_type = type === 'live' ? +playerData.live_type : null;
    let live_subtitle = type === 'live' ? +playerData.live_subtitle : null;
    let live_delay = type === 'live' ? +playerData.live_delay : null;
    // 
    const schedulerConfig = {
      url: playerData && playerData.dispatch_server,
      live_type, // 0- 1-
      live_subtitle, // 0- 1-
      live_delay, // 0- 1-
      webinar_id: type === 'live' ? opt.liveOption.roomId.trim() : opt.vodOption.recordId.trim(),
      uid: playerData && playerData.log_info.uid,
      bu: 1, //  pass 1
      // 
      // 
      quality: schedulerOption && schedulerOption.quality ? schedulerOption.quality :
          type === 'live' ? [] : (playerData && needDecrypt ? playerData.safe_definition : playerData.definition), //    
      defaultDef: defaultDefinition || '480p', // 
      app_type: type === 'live' ? playerData && playerData.app_type : null,
      cipher_key: needDecrypt && playerData.safe_server ? playerData.safe_server.cipher_key : null,
      cdn_priority: playerData && playerData.cdn_priority || null
    };

    // 
    const liveTimeshiftConfig = {
      enable: false,
      isLive: type === 'live',
      // 
      isOpenTimeshift: type === 'live' ? live_type === 1 : undefined,
      // 
      live_duration: type === 'live' && live_type === 1 ? playerData.live_duration : undefined,
    }

    let align = '';
    switch (playerData && playerData.watermark.position) {
      case 1: // 
        align = 'tl';
        break;
      case 2: // 
        align = 'tr';
        break;
      case 3: // 
        align = 'br';
        break;
      case 4: // 
        align = 'bl';
        break;
      default:
        break;
    }

    // 
    const watermarkPaasConfig = {
      url: playerData && playerData.watermark.url,
      align: align, //  tl | tr | bl | br 
      position: ['20px', '20px'], // px,vh,vw,%
      size: ['80px', '35px'], // px,vh,vw,%
      enable: playerData && playerData.watermark.url !== '', // 
      alpha: 100
    };

    // 
    const barrageConfig = {
      enable: true,
      // 
      positionRange: [0, 0.25], // [startPostion: number, endPosition: number] 
      direction: 'right', //  right, left : right
      speed: 10000,
      style: {
        fontSize: 12, // 
        fontFamily: 'Microsoft Yahei', // 
        color: '#FFF', // 
        border: 'none',
        textShadow: null,  //  1px 1px 0px #fff https://www.w3school.com.cn/cssref/pr_text-shadow.asp
        borderWidth: 0, // px
        padding: '0', // 
        opacity: 80, // 
        backgroundColor: 'none' // 
      }
    };

    // 
    const pursueConfig = {
      enable: false,
      flv: { bufferMax: 10, remain: 2, pursueSpeed: 1.2 },
      hls: { bufferMax: 20, remain: 8, pursueSpeed: 1.2 },
      mode: 'pursue'
    };

    let vod_subtitle = type === 'live' ? null : playerData.subtitle_info && playerData.subtitle_info['srt'] && playerData.subtitle_info['srt'].length > 0;
    // 
    const subtitleConfig = {
      enable: false, // 
      subtitle_info: playerData.subtitle_info
    }

    /*
     
     
     */
    let success_flag = false;
    let loadedmetadata_flag = false;
    let progress_flag = false;
    let play_flag = false;
    let loadeddata_flag = false;
    vhallplayer.on(VhallPlayer.PROGRESS_IN, (e) => {
      // 
      if (success_flag !== true) {
        progress_flag = { data: e };
      }
      // 
      vhallplayer.emit2All(VhallPlayer.PROGRESS, e);
    });

    vhallplayer.on(VhallPlayer.LOADEDMETADATA_IN, (e) => {
      if (success_flag !== true) {
        loadedmetadata_flag = { data: e };
      }
      vhallplayer.emit2All(VhallPlayer.LOADEDMETADATA, e);
    });

    vhallplayer.on(VhallPlayer.PLAY_IN, (e) => {
      if (success_flag !== true) {
        play_flag = { data: e };
      }
      vhallplayer.emit2All(VhallPlayer.PLAY, e);
    });

    vhallplayer.on(VhallPlayer.LOADED_IN, (e) => {
      if (success_flag !== true) {
        loadeddata_flag = { data: e };
      }
      vhallplayer.emit2All(VhallPlayer.LOADED, e);
    });

    vhallplayer.on('ready', async () => {
      let markData;
      if (type === 'vod' && playerData.cue_point !== '') {
        await axios.get(playerData.cue_point).then(
            res => {
              markData = res.data.usrdata;
              // cue_pointSDK
              window.VhallMsg && VhallMsg.emitBroadcast({ type: 'cue_point', data: JSON.stringify(res.data) });

              // SDK
              vhallplayer.on('timeupdate', t => {
                window.VhallMsg && VhallMsg.emitBroadcast({ type: 'time_update', data: t });
              })
            },
            () => {
              // console.warn('cue_point')
            }
        )
      } else if (type === 'live') {
        vhallplayer.on('timeupdate', (e) => {
          // const buffered = vhallplayer.getBuffered();
          // const bufferedLen = buffered.length;
          // let buffer = 0;
          // // FlashPlayerbufferedend
          // if (vhallplayer.player._type === 'FlashPlayer') {
          //   buffer = bufferedLen >= 1 ? buffered[0][1] : 0;
          // } else {
          //   buffer = bufferedLen >= 1 ? buffered.end(bufferedLen - 1) : 0;
          // }
          // // 
          // let delay = buffer - vhallplayer.getCurrentTime();
          // // 2020.03.12
          // delay = delay < 120 ? delay : 10;
          // // +1.5s
          // delay += 1.5;
          // if (vhallplayer.currentDefinition.def !== 'same') {
          //   // +2s
          //   delay += 2;
          // }
          // // 
          // delay = delay < 0 ? 0 : delay;
          window.VhallMsg && VhallMsg.emitBroadcast({ type: 'delay', data: { delay: e.delay } });
        })
      }

      success_flag = true;
      success({
        vhallplayer: new VhallInterface({ vhallplayer, isLive: type === 'live', live_type, live_subtitle, vod_subtitle, videoNode: `#${opt.videoNode}`, markPoints: markData || [], store }),
        markPoints: markData || []
      });
      // 
      if (progress_flag) {
        vhallplayer.emit2All(VhallPlayer.PROGRESS, progress_flag.data);
      }
      if (loadedmetadata_flag) {
        vhallplayer.emit2All(VhallPlayer.LOADEDMETADATA, loadedmetadata_flag.data);
      }
      if (play_flag) {
        vhallplayer.emit2All(VhallPlayer.PLAY, play_flag.data);
      }
      if (loadeddata_flag) {
        vhallplayer.emit2All(VhallPlayer.LOADED, loadeddata_flag.data);
      }
    });

    try {
      const Params = {
        x5: !!judgeX5(),
        autoplay: opt.autoplay !== false,
        accountId: opt.accountId,
        // 
        type: type === 'live' ? opt.liveOption.type : playerData.default_server.mp4_domainnames ? 'native' : 'hls',
        isLive: type === 'live', // 
        liveOption: type === 'live' ? opt.liveOption : null,
        vodOption: type === 'live' ? null : opt.vodOption,
        id: opt.videoNode, // dom
        poster: opt.poster, // 
        hlsurl: type === 'live' ? null : (needDecrypt ? playerData.dispatch_server + playerData.safe_server.uri : playerData.dispatch_server + playerData.default_server.uri),
        nativeurl: type === 'live' ? null : playerData.dispatch_server + playerData.default_server.uri,
        plugin_reporter: JSON.stringify(reportConfig),
        other_option: JSON.stringify(otherOption), // 
        plugin_scheduler: JSON.stringify(schedulerConfig),
        plugin_timeshift: JSON.stringify(liveTimeshiftConfig),
        plugin_watermark:
            opt.hasOwnProperty('watermarkOption') && opt.watermarkOption.enable
                ? JSON.stringify(opt.watermarkOption)
                : JSON.stringify(watermarkPaasConfig),
        plugin_barrage: opt.hasOwnProperty('barrageSetting') ? JSON.stringify(opt.barrageSetting) : JSON.stringify(barrageConfig),
        plugin_subtitle:
            opt.hasOwnProperty('subtitleOption') && opt.subtitleOption.enable
                ? JSON.stringify(Object.assign(subtitleConfig, opt.subtitleOption))
                : '{ "enable": false }',
        plugin_marquee:
            opt.hasOwnProperty('marqueeOption') && opt.marqueeOption.enable
                ? JSON.stringify(opt.marqueeOption) : '{ "enable": false }',
        plugin_pursue:
            opt.hasOwnProperty('pursueOption') && opt.pursueOption.enable
                ? JSON.stringify(Object.assign(pursueConfig, opt.pursueOption))
                : '{"enable":false}',
        plugin_thorn: JSON.stringify(
            {
              ...({enable: true}),
              ...(opt.hasOwnProperty('thornOption') ? opt.thornOption : {})
            }
        ),
        peer5Option: {
          open: false, // 
          customerId: '', // todo customerId - 
          fallback: true, // peer5 sdk false
          ...(opt.peer5Option || {})
        }
      }
      if(playerData.rtc_schedule_server) {
        Params['rtc_schedule_server'] = playerData.rtc_schedule_server;
      }
      vhallplayer.info('info', 'params biz => ', opt)
      vhallplayer.info('info', 'params h5 => ', Params)
      await vhallplayer.init(Params); // => 
    } catch (err) {
      failure && failure({ code: '611000', message: ': ' + err.message, data: {} });
    }
  }
}

// 
for (let i in EVENT) {
  VhallPlayer[i] = EVENT[i]
}

if (process.env.NODE_ENV === 'development') {
  VhallPlayer._version_ = '2.4.0'
}

window.VhallPlayer = VhallPlayer

export default VhallPlayer
