import Plugin from '../core/Plugin';
import { PlayerEvent } from '../PlayerEvents';

/**
 * 
 *
 * @class Fullscreen
 * @extends {Plugin}
 */
export default class Fullscreen extends Plugin {
  constructor() {
    super();
  }

  init(opts = {}) {
    super.init(opts);
    this._allConfig = opts;
    let self = this;
    // srcchange 
    this.player.on(PlayerEvent.SRC_CHANGED, this._handleSrcChange.bind(this));

    // 
    Object.defineProperty(this.player, 'displayState', {
      get() {
        return self._displayState || 'normal';
      },
      set(v) {
        self._displayState = v;
      },
      configurable: true
    });

    // 
    Object.defineProperty(this.player, 'isFullscreen', {
      get() {
        return typeof self._isFullscreen === 'undefined' ? !!self.isFullscreen : self._isFullscreen;
      },
      configurable: true
    });

    // 
    this.player.enterFullscreen = this.enterFullscreen.bind(this);
    // 
    this.player.exitFullscreen = this.exitFullscreen.bind(this);

    // ()
    ['fullscreenchange', 'webkitfullscreenchange', 'mozfullscreenchange', 'MSFullscreenChange'].forEach(item => {
      document.addEventListener(item, e => {
        // this.isFullscreen IEIEBoolen
        this._isFullscreen = !!this.isFullscreen;
        if (this._isFullscreen && this.player._clickExit) {
          this.player._clickExit = undefined;
        }
        this._fullscreenTarget = e.target;
        self._displayState = this._isFullscreen ? 'fullscreen' : 'normal';
        // this.info('info', `: ${!!this.isFullscreen}; isFullscreen=${this._isFullscreen}; displayState=${self._displayState}`);
        self.player.emit2All('fullscreenchanged', self._displayState);
      });
    });

    // ()
    window.addEventListener('resize', e => {
      // this.info('info', `resize: ${this.isFullscreen}, ${this._isFullscreen}, ${self._displayState}, ${this.player._clickExit}`);
      // MSE
      if (this._allConfig.x5 && this.player && this.player.player && this.player.player._type !== 'NativePlayer' && ((this._isFullscreen && this.isFullscreen && self._displayState === 'normal') || this.player._clickExit)) {
        if (this.player._clickExit) {
          this.info('info', 'resize, x5API');
          this.player._clickExit = undefined;
        } else {
          this.info('info', 'resize, x5');
        }
        this._isFullscreen = false;
        self._displayState = 'normal';
        // this.info('info', `: displayState=${self._displayState}`);
        this.player.emit2All('fullscreenchanged', self._displayState);
      }
    });
  }

  _handleSrcChange() {
    // this.info('info', `src: ${this.isFullscreen}, ${this._isFullscreen}, ${this._displayState}, ${this.player._clickExit}`);
    if (this._allConfig.x5 && this.player && this.player.player && this.player.player._type !== 'NativePlayer' && this._isFullscreen && this.isFullscreen && this._displayState === 'fullscreen') {
      this.info('info', 'src, ');
      this._isFullscreen = false;
      self._displayState = 'normal';
      this.player.emit2All('fullscreenchanged', self._displayState);
    }
  }

  /**
   * 
   *
   * @memberof Fullscreen
   */
  enterFullscreen() {
    // H5  el video
    let el = this.player.getRoot(); // div
    if (el.webkitEnterFullScreen) {
      // Chrome
      el.webkitEnterFullScreen();
    } else if (el.mozRequestFullScreen) {
      // FireFox
      el.mozRequestFullScreen();
    } else if (el.requestFullscreen) {
      // w3c
      el.requestFullscreen();
    } else if (el.msRequestFullscreen) {
      // IE11
      el.style.width = '100%';
      el.style.height = '100%';
      el.msRequestFullscreen();
    } else {
      // ios
      let sdk = this.player;
      let videoEl = sdk.player.video; // video
      if (videoEl.webkitEnterFullscreen || videoEl.enterFullScreen) {
        // iosloadedmetadata 
        videoEl.webkitEnterFullscreen && videoEl.webkitEnterFullscreen();
        videoEl.enterFullScreen && videoEl.enterFullScreen();
        videoEl.msRequestFullscreen && videoEl.msRequestFullscreen();
      }
    }

    this.player.displayState = 'fullscreen';
  }

  /**
   * 
   *
   * @memberof Fullscreen
   */
  exitFullscreen() {
    if (document.exitFullscreen) {
      // chrome
      document.exitFullscreen();
    } else if (document.webkitExitFullscreen) {
      document.webkitExitFullscreen();
    } else if (document.mozCancelFullScreen) {
      document.mozCancelFullScreen();
    } else if (document.msExitFullscreen) {
      // IE11SaaS
      let el = this.player.getRoot();
      el.style.width = '';
      el.style.height = '';
      document.msExitFullscreen();
    } else {
      // ios
      const video = this.player.player.video;
      if (video.webkitExitFullscreen) {
        video.webkitExitFullscreen();
      }
    }
    this.player.displayState = 'normal';
  }

  destroy() {
    super.destroy();
    delete this.player.displayState;
    delete this.player.isFullscreen;
  }

  /**
   * 
   *
   * @readonly
   * @memberof Fullscreen
   * @returns  document  fullscreen 
   */
  get isFullscreen() {
    return document.fullScreen || document.webkitIsFullScreen || document.mozFullScreen || document.msFullscreenElement || false;
  }

  /**
   *  
   *
   * @readonly
   * @memberof Fullscreen
   * @returns 
   */
  get fullscreenElement() {
    return (
      document.fullscreenElement ||
      document.webkitCurrentFullScreenElement ||
      document.mozFullScreenElement ||
      this._fullscreenTarget ||
      null
    );
  }

  static get type() {
    return 'plugin_fullscreen';
  }
}
