/**
 * 
 *
 * @export
 * @class Pursue
 */
export default class Pursue extends Plugin {
  constructor() {
    super();

    this.bufferMax = -1;
    this.remain = 2;
    this.speed = 1.2;
    this.mode = 'pursue';

    this._isPursuing = false;
  }

  init(opts = {}) {
    if (opts['isLive'] && opts['isOpenTimeshift'] && opts['type'] === 'hls') {
      this.info('info', '');
      return;
    }

    super.init(opts);
    this._allConfig = opts;
    this.pursueConfig = JSON.parse(opts[Pursue.type]);

    if (!this.pursueConfig['enable']) {
      this.info('info', '');
      return;
    }

    this._isLive = opts['isLive'];

    // 
    if (!this._isLive) {
      this.info('info', '');
      return;
    }

    if (this.player.player._type === 'FlashPlayer') {
      this.info('info', 'flash');
      return;
    }

    // , 
    if (TencentInject.isSupported()) {
      this.info('info', ',x5');
      return;
    }

    const type = this._allConfig['type'];
    const cfg = this.pursueConfig[type] || { bufferMax: 10, remain: 2, pursueSpeed: 1.2 };
    this.bufferMax = cfg['bufferMax'];
    this.remain = cfg['remain'];
    this.speed = cfg['pursueSpeed'];
    this.mode = this.pursueConfig['mode'];

    this.info(
      'info',
      `:${type},Buffer:${this.bufferMax}s, ${this.speed}${this.remain}s.`
    );
    this._handleEvents();
  }

  _handleEvents() {
    this.player.on(PlayerEvent.TIMEUPDATE, this.__onTimeupdate.bind(this));
    this.player.on(PlayerEvent.BUFFER_EMPTY, this.__onBufferEmpty.bind(this));
  }

  __onTimeupdate(e) {
    // 
    // 1: buffer 23:buffer
    const player = this.player;
    const buffered = player.getBuffered();
    const bufferedLen = buffered.length;
    let buffer = bufferedLen >= 1 ? buffered.end(bufferedLen - 1) : 0;
    // 
    let elapsed = buffer - player.getCurrentTime();
    //   
    if (elapsed > this.bufferMax || (this._isPursuing && elapsed > this.remain)) {
      // 
      const mode = this.mode;
      switch (mode) {
        case 'pursue':
        // bufferMaxbufferMax10 30
          if (elapsed > 6 * this.bufferMax) {
            this.jumpTo(buffer - this.remain);
          } else {
            this.run();
          }
          break;
        case 'seek':
          this.jumpTo(buffer - this.remain);
          break;
        default:
          this.info('warn', `${mode}`);
          break;
      }
    } else {
      this.walk();
    }

    // this.info('debug', `${player.getCurrentTime()},${buffer},${elapsed},${this.isPursuing}`);
  }

  __onBufferEmpty(e) {
    this.walk();
  }

  /**
   * 
   *
   * @memberof Pursue
   */
  run() {
    this._isPursuing = true;
    this.player.setPlaybackRate(this.speed);
  }

  /**
   * 
   *
   * @memberof Pursue
   */
  walk() {
    this._isPursuing = false;
    this.player.setPlaybackRate(1);
  }

  /**
   * seek 
   *
   * @memberof Pursue
   */
  jumpTo(t) {
    if (t <= 0) {
      t = 0;
    }

    this.player.setCurrentTime(t);
    this.player.setPlaybackRate(1);
  }

  destroy() {
    super.destroy();
    this.bufferMax = -1;
    this.remain = 2;
    this.speed = 1.2;
    this._isPursuing = false;

    // 
    this.player.off(PlayerEvent.TIMEUPDATE, this.__onTimeupdate);
    this.player.off(PlayerEvent.BUFFER_EMPTY, this.__onBufferEmpty);
    this.player = null;
  }

  /**
   * 
   *
   * @readonly
   * @memberof Pursue
   */
  get isPursuing() {
    return this._isPursuing;
  }

  static get type() {
    return 'plugin_pursue';
  }
}
