import UIComponent from "../../common/UIComponent";
import "./index.css";
import markPoints from '../markpoints';

export default class Progress extends UIComponent {
  constructor(elementId, interfaces, videoId, markPoints) {
    super();
    this.markPoints = markPoints;
    this.interfaces = interfaces;


    this.elementId = elementId;
    this.videoNode = document.querySelector(`${videoId}`);
    this.init();

    this.interfaces.on(VhallPlayer.LOADED, (e) => {
      this.progressContainer.setAttribute("class", "vhallPlayer-progress-container display");
    })

    this.interfaces.on(VhallPlayer.TIMEUPDATE, () => {
      // TODO: IOS IndexSizeError: The index is not in the allowed range.  
      if (this.interfaces.getBuffered().length <= 0) { return; }

      let progressLoadValue = 0;
      // FlashPlayerbufferedend
      if (this.interfaces.vhallplayer.player._type === 'FlashPlayer') {
        const buffered = this.interfaces.getBuffered();
        progressLoadValue = buffered[0][1] / this.interfaces.getDuration() * 100;
      } else {
        progressLoadValue = ((this.interfaces.getBuffered().end(this.interfaces.getBuffered().length - 1) / this.interfaces.getDuration()) * 100);
      }
      this.progressLoad.style.width = progressLoadValue + "%";

      // seek 
      if (this.isSeeking) { return; }

      const progressPlayValue = ((this.interfaces.getCurrentTime() / this.interfaces.getDuration()) * 100);
      if (progressPlayValue > 100) { return; }
      this.progressPlay.style.width = progressPlayValue + "%";
      this.scrubber.style.left = progressPlayValue + "%";
    });
  }

  init() {
    this.progressContainer = document.createElement("div");
    this.progressContainer.setAttribute("class", "vhallPlayer-progress-container hide");
    this.progressContainer.onmousedown = this.handleMouseDown.bind(this)

    this.progressLoad = document.createElement("div");
    this.progressLoad.setAttribute("class", "vhallPlayer-progress-load");

    this.progressPlay = document.createElement("div");
    this.progressPlay.setAttribute("class", "vhallPlayer-progress-play");

    this.scrubber = document.createElement("div");
    this.scrubber.setAttribute("class", "vhallPlayer-progress-scrubber");
    this.scrubber.innerHTML = `<i></i>`;

    this.progressContainer.appendChild(this.progressLoad);
    this.progressContainer.appendChild(this.progressPlay);
    this.progressContainer.appendChild(this.scrubber);

    this.insertElement(this.elementId, this.progressContainer);

    // toDo add markdoms
    new markPoints('.vhallPlayer-progress-container', this.interfaces, this.markPoints)

  }

  handleMouseDown(event) {
    this.isSeeking = true;  //  seek
    const mouseX = event.clientX;  // 
    let currentTime = null;   // 
    // 
    currentTime = event.offsetX / this.progressContainer.offsetWidth * this.interfaces.getDuration();
    // 
    this.scrubber.style.left = event.offsetX / this.progressContainer.offsetWidth * 100 + '%';
    // 
    this.progressPlay.style.width = event.offsetX / this.progressContainer.offsetWidth * 100 + '%';
    // 
    this.progressLoad.style.width = 0 + "%";
    // 
    let left = event.offsetX;

    document.onmousemove = event => {
      // 
      const moveL = event.clientX - mouseX;
      // 
      const newW = moveL + left;
      // 
      currentTime = newW / this.progressContainer.offsetWidth * this.interfaces.getDuration();
      // 
      if (newW / this.progressContainer.offsetWidth < 0 || newW / this.progressContainer.offsetWidth > 1) { return; }
      // 
      this.scrubber.style.left = newW / this.progressContainer.offsetWidth * 100 + '%';
      // 
      this.progressPlay.style.width = newW / this.progressContainer.offsetWidth * 100 + '%';
    }

    document.onmouseup = (e) => {
      // 
      this.interfaces.setCurrentTime(currentTime);
      // 
      this.progressLoad.style.width = 0 + "%";
      // seek
      this.isSeeking = false;
      // 
      document.onmousemove = null;
      // 
      document.onmouseup = null;
    }
  }
}