import { KV } from '../../core/Constant';
import Hls from './hls';

/**
 *
 *
 * @export
 * @class HlsPlayer
 * @extends {Hls}
 */
export default class HlsPlayer extends Hls {
  constructor(config = {}) {
    super(config);
    this.playedTime = 0;
  }

  getDownloadSize() {
    return this.getStore().getKV(KV.DownloadSize);
  }

  getEstimateNetSpeed() {
    return this.abrController.estimateNetSpeed;
  }

  setSrc(val) {
    return new Promise((resolve, reject) => {
      this.playedTime = this.getCurrentTime();
      this.playBackRate = this.getPlaybackRate();
      super.setSrc(val);
      // 
      this.networkControllers.forEach(component => {
        component.stopLoad();
      });
      // 
      this.once(Hls.Events.MEDIA_STREAM_DETACHED, () => {
        this.loadSource(val);
        this.attachMedia(this.video);
        // this.once(Hls.Events.MEDIA_ATTACHED, () => {
        //   // this.play();
        //   //  
        //   if (!this.getIsLive()) {
        //     this.setCurrentTime(this.playedTime);
        //     this.setPlaybackRate(this.playBackRate);
        //   }
        // })

        // v2.3.9 ,  ondurationchange => onloadedmetadata => onloadeddata
        // v2.3.9  ;  setQuality | set currentDefinition 
        this.video.ondurationchange = () => {
          resolve();
        }

        // ie:
        this.video.onloadeddata = () => {
          // 
          // seek(0),seeksetSrcseek
          if (!this.getIsLive() && this._setSrcCount !== 1) {
            // this.setCurrentTime(this.playedTime);
            this.setPlaybackRate(this.playBackRate);
          }
        };
      });
      this.detachMedia();
    });
  }
}
